.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  background-color: #151515;
  font-family: 'Raleway', sans-serif;
}

.user-info {
  display: grid;
  justify-content: end;
  margin-right: 30px;
}

input {
  margin-left: 10px;
  padding-left: 10px;
}


a {
  text-decoration: none;
  color: inherit;
}

h1 {
  color: white;
}

#user-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  margin: 10px;
}

.user-box {
  display: grid;
  height: 200px;
  border-radius: 10px;
  align-content: center;
  color: rgb(238, 235, 235);
  font-size: 22px;
  cursor: pointer;
}

.movie-container #movie-title {
  display: none;
}

.movie-container:hover #movie-title {
  display: block;
}

#search-container {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
}

i {
  color: white;
  cursor: pointer;
  display: grid;
  justify-self: end;
  margin-top: 10px;
  margin-right: 10px;
  height: 20px;
}

p {
  color: white;
}

h2 {
  color: white;
  margin-bottom: 5px;
}

#search-container {
  margin: 10px;
}

.catalog-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  margin: 15px;
}

.movie-container {
  height: 300px;
  border-radius: 15px;
  display: grid;
}

.section {
  text-decoration: underline;
  font-weight: bold;
}

#link-header {
  display: grid;
  grid-template-columns: 1fr 1fr 3fr;
  height: 50px;
  align-content: center;
  font-size: 20px;
  color: white;
}

#link-header > a:hover { 
  color: rgb(182, 178, 178);
}

#logo > a:hover {
  color: white;
}

#logo {
  display: grid;
  justify-content: end;
  margin-right: 20px;
  align-content: center;
  color: rgb(216, 15, 15);
}

.movie-detail-container > p {
  margin-right: 50px;
  margin-left: 50px;
}

.movie-detail-container > img {
  max-width: 30%;
  max-height: 380px;
  border-radius: 15px;
}

.movie-container > a {
  display: grid;
  align-content: end;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
